<template>
	<div class="users">
		<div class="container filter">
			<div class="actions-container">
				<div class="actions">
					<div class="action" :class="{opened: showCalendar}" @click="showCalendar = !showCalendar, $emit('show-fullscreen-blur', true)">
						<Calendar/>
						<div class="name">За 1 день</div>
						<MenuArrow/>
					</div>
					<div class="action">
						<FilterIcon/>
						<div class="name">Фильтр</div>
						<MenuArrow/>
					</div>
				</div>
				<div class="filter-buttons">
					<div class="btn">Выгрузить</div>
					<div class="btn apply">Добавить контакты</div>
				</div>
			</div>
			<div
					class="show-calendar"
					v-if="showCalendar"
			>
				<VueCtkDateTimePicker
						v-model="initialDate"
						:inline="true"
						:range="true"
						:noHeader="true"
						:custom-shortcuts="customShortcuts"
						format="YYYY-MM-DD"
				></VueCtkDateTimePicker>
				<div class="buttons">
					<div class="btn" @click="handleClose">Отмена</div>
					<div class="btn apply" @click="handleChange" :class="{disabled: initialDate.start == null}">Обновить</div>
				</div>
			</div>
			<div class="filters-values" v-if="filterValues" @click="destroyInitialDate">
				<div class="filter-value">
					{{initialDate.start | moment("D MMM") | removeDot}}
					<span v-if="initialDate.end != null">-</span>
					{{initialDate.end | moment("D MMM") | removeDot}} {{initialDate.start | moment("YYYY")}} г.
					<img src="../assets/svg/unset-filter.svg" class="selector">
				</div>
			</div>
		</div>
		<div class="container">
			<table>
				<tr>
					<th>
						<img src="../assets/svg/user-circle.svg" class="selector">
					</th>
					<th>Пользователь</th>
					<th>Дата регистрации</th>
					<th>Последняя активность</th>
					<th>Последнее действие</th>
					<th>Продукт</th>
					<th>
						<div class="select">
							Отобразить
							<span>15</span>
							<img src="../assets/svg/menu-arrow.svg" class="arrow">
						</div>
					</th>
				</tr>
				<tr>
					<td colspan="7" class="separator"></td>
				</tr>
				<tr class="user" v-for="user in users" v-if="users.length">
					<td>
						<img src="../assets/svg/user-circle.svg" class="selector">
					</td>
					<td>
						<div class="name">
							<img src="../assets/svg/locked.svg" :class="{locked: user.locked}">
							{{user.name}}
						</div>
						<div class="email">{{user.email}}</div>
					</td>
					<td :class="{locked: user.locked}">{{user.registration | moment("dddd D, YYYY") | capitalize}}</td>
					<td :class="{locked: user.locked}">{{user.registration | moment("dddd D, YYYY") | capitalize}}</td>
					<td :class="{locked: user.locked}">view_landing_course1</td>
					<td :class="{locked: user.locked}">Как наладить отнош</td>
					<td>
						<div class="actions">
							<img src="../assets/svg/pencil.svg" class="pencil">
							<img src="../assets/svg/trash.svg" class="trash">
						</div>
					</td>
				</tr>
				<tr v-if="!users.length">
					<td colspan="7" style="text-align: center">Согласно выбранному фильтру записи не обнаружены</td>
				</tr>
				<tr>
					<td colspan="7" class="separator"></td>
				</tr>
			</table>
		</div>
	</div>
</template>

<script>
	import users
		from "../json/users.json"
	import moment
		from 'moment'
	import Calendar
		from "./svg/Calendar"
	import FilterIcon
		from "./svg/Filter-icon"
	import MenuArrow
		from "./svg/Menu-arrow"

	export default {
		name: "Users",
		components: {
			MenuArrow,
			Calendar,
			FilterIcon
		},
		data: () => ({
			aUsers: users,
			users,
			showCalendar: false,
			initialDate: {
				start: null,
				end: null
			},
			filterValues: false,
			customShortcuts: [
				{
					key: 'alltime',
					label: 'Весь срок',
					value: () => {
						const countUsers = users.length - 1
						return {
							start: moment(users[0].registration),
							end: moment(users[countUsers].registration)
						}
					}
				},
				{
					key: 'today',
					label: 'Сегодня',
					value: 'day'
				},
				{
					key: 'yesterday',
					label: 'Вчера',
					value: '-day'
				},
				{
					key: 'last7Days',
					label: 'Последние 7 дней',
					value: 7
				},
				{
					key: 'last30Days',
					label: 'Последние 30 дней',
					value: 30
				},
				{
					key: 'thisMonth',
					label: 'В этом месяце',
					value: 'month'
				},
				{
					key: 'lastMonth',
					label: 'Прошлый месяц',
					value: '-month'
				}
			]
		}),
		mounted() {
			moment.locale('ru')
		},
		methods: {
			// actionShowCalendar() {
			// 	this.showCalendar == false
			// 		? this.showCalendar = true
			// 		: this.showCalendar = false
			// },
			handleChange() {
				// console.log(this.initialDate)
				if (this.initialDate.start != null) {
					this.startDate = Date.parse(this.initialDate.start)
					this.endDate = Date.parse(this.initialDate.end)

					this.users = this.aUsers

					if (this.endDate > 0) {
						this.users = this.users.filter(user => {
							return Date.parse(user.registration) >= this.startDate && Date.parse(user.registration) <= this.endDate
						})
					} else {
						this.users = this.users.filter(user => {
							return Date.parse(user.registration) >= this.startDate && Date.parse(user.registration) <= this.startDate + 24 * 3600
						})
					}

					this.showCalendar = false
					this.$emit('show-fullscreen-blur', false)
					this.filterValues = true
				}

				// console.log('Start date: ', this.startDate)
				// console.log('End date: ', this.endDate)
			},
			handleClose() {
				this.showCalendar = false
				this.$emit('show-fullscreen-blur', false)
			},
			destroyInitialDate() {
				this.initialDate = {
					start: null,
					end: null
				}
				this.filterValues = false
				this.users = this.aUsers
			}
		},
		filters: {
			capitalize: function (value) {
				if (!value) return ''
				value = value.toString()
				return value.charAt(0).toUpperCase() + value.slice(1)
			},
			removeDot(value) {
				if (!value) return ''
				return value.replace('.', "");
			}
		}
	}
</script>
